<template>
    <div class="tutorial-manager">
        <div class="button-part">
            <el-button
            type="success"
            @click="handleAdd()">添加训练教程</el-button>
        </div>
        <!-- 教程表格 -->
         <el-table
            v-loading="listLoading"
            :data="list"
            element-loading-text="Loading"
            border
            fit
            highlight-current-row
            row-key="tutoId"
            :tree-props="{children: 'children', hasChildren: 'hasChildren'}"
            >
            <el-table-column align="center" label="教程ID" width="150" >
                <template slot-scope="scope">
                {{ scope.row.tutoId}}
                </template>
            </el-table-column>
            <el-table-column label="教程标号" width="150" align="center">
                <template slot-scope="scope">
                {{ scope.row.titleNum }}
                </template>
            </el-table-column>
            <el-table-column label="教程标题" align="center">
                <template slot-scope="scope">
                <span>{{ scope.row.titleContent }}</span>
                </template>
            </el-table-column>
             <el-table-column label="相关练习题" align="center" width="150" >
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    type="primary"
                    @click="handleProblemManage(scope.$index, scope.row)">管理练习题</el-button>
                </template>
            </el-table-column>
            <el-table-column label="操作" width="260"  align="center">
                  <template slot-scope="scope">
                    <el-button
                    size="mini"
                    type="primary"
                    @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    <el-button
                    size="mini"
                    type="success"
                    @click="handleDetail(scope.$index, scope.row)">详情</el-button>
                </template>
            </el-table-column>

            </el-table>
         <!-- 教程表格结束 -->

         <!-- 教程编辑框 -->
         <el-dialog :title="showText" :visible.sync="dialogFormVisible">
            <el-form :model="editInfo" :rules="editInfoRules">
                <el-form-item label="教程标题" :label-width="formLabelWidth" prop="titleContent">
                <el-input v-model="editInfo.titleContent" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="教程标号" :label-width="formLabelWidth" prop="titleNum">
                <el-input v-model="editInfo.titleNum" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="教程内容" :label-width="formLabelWidth" prop="content">
                <el-input
                    type="textarea"
                    :autosize="{ minRows: 14}"
                    placeholder="请输入内容"
                    v-model="editInfo.content">
                    </el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="addTutorial()">确 定</el-button>
            </div>
        </el-dialog>
        <!-- 教程编辑框结束 -->

        <!-- 问题编辑框开始 -->
         <el-dialog :title="showText2" :visible.sync="editProblemShow" >
            <el-form :model="editPInfo" :rules="editPInfoRules">
                <el-form-item label="问题编号" :label-width="formLabelWidth" prop="probId">
                <el-input v-model="editPInfo.probId" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="题目内容" :label-width="formLabelWidth" prop="content">
                <el-input
                    type="textarea"
                    :autosize="{ minRows: 14}"
                    placeholder="请输入内容"
                    v-model="editPInfo.content">
                    </el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="addProblem()">确 定</el-button>
            </div>
        </el-dialog>
        <!-- 问题编辑框结束 -->

         <!-- 题解编辑框开始 -->
         <el-dialog :title="showText3" :visible.sync="editAnswerShow" >
            <el-form :model="editAInfo" :rules="editAInfoRules">
                <el-form-item label="题解内容" :label-width="formLabelWidth" prop="text">
                <el-input
                    type="textarea"
                    :autosize="{ minRows: 14}"
                    placeholder="请输入内容"
                    v-model="editAInfo.text">
                    </el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="editAnswerShow = false">取 消</el-button>
                <el-button type="primary" @click="addAnswer()">添加</el-button>
                <el-button type="warning" @click="updateAnswer()">修改</el-button>
            </div>
        </el-dialog>
        <!-- 题解编辑框结束 -->

        <!-- 问题管理框 -->
             <el-dialog title="相关练习管理" :visible.sync="problemDialogShow" width="60%" class="problem-box">
                 <el-button
                    type="success"
                    @click="handleAddp()">添加相关练习题</el-button>
                <el-table :data="problemList" width="700">
                    <el-table-column property="probId" label="问题ID" width="150" align="center"></el-table-column>
                    <el-table-column property="probTitle" label="题名" width="250" align="center"></el-table-column>
                      <el-table-column type="expand" label="题目内容" width="150" @click="test()">
                    <template slot-scope="props" >
                        <div class="problem-content">
                            {{props.row.content}}
                        </div>
                    </template>
                    </el-table-column>
                <el-table-column label="操作" width="320"  align="center">
                  <template slot-scope="scope">
                    <el-button
                    size="mini"
                    type="primary"
                    @click="handleEditP(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDeleteP(scope.$index, scope.row)">删除</el-button>
                    <el-button
                    size="mini"
                    type="warning"
                    @click="handleShowData(scope.$index, scope.row)">测试数据</el-button>
                    <el-button
                    size="mini"
                    type="success"
                    @click="handleShowAnswer(scope.$index, scope.row)">题解</el-button>
                </template>
            </el-table-column>
                </el-table>
            </el-dialog>
        <!-- 问题管理框结束 -->

        <!-- 测试数据编辑框 -->
         <el-dialog title="管理测试数据" :visible.sync="editDataShow" >
            <el-form :model="editDInfo" :rules="editDInforRules">
                <el-form-item label="输入数据" :label-width="formLabelWidth" prop="in">
                <el-input v-model="editDInfo.in" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="输出数据" :label-width="formLabelWidth" prop="out">
                <el-input v-model="editDInfo.out" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="addData()">确 定</el-button>
            </div>
        </el-dialog>
        <!-- 测试数据编辑框结束 -->

        <!-- 测试数据管理框 -->
        <el-dialog title="测试数据管理" :visible.sync="dataDialogShow" width="63%" class="problem-box">
                 <el-button
                    type="success"
                    @click="handleAddD()">添加测试数据</el-button>
                <el-table :data="dataList" width="700">
                    <el-table-column property="dataId" label="数据ID" width="150" align="center"></el-table-column>
                    <el-table-column property="in" label="输入" width="250" align="center"></el-table-column>
                    <el-table-column property="out" label="输出" width="250" align="center"></el-table-column>
                <el-table-column label="操作" width="260"  align="center">
                  <template slot-scope="scope">
                    <el-button
                    size="mini"
                    type="primary"
                    @click="handleEditD(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDeleteD(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
                </el-table>
            </el-dialog>
        <!-- 测试数据管理框结束 -->
    </div>
</template>

<script>
import axios from 'axios'
import{addData,
       getData,
       deleteData,
       updateData} from '../../network/data.js'
import{addTutorial,
getAllTutorialsForOneSubject,
getAllTutorialsForTwoSubject,
getTutorialById,
updateTutorial,
deleteTutorial}from '../../network/tutorial.js'

import{addProblem,
updateProblem,
getAnswer,
addAnswer,
updateAnswer} from '../../network/problem.js'
import { getAllBulletin } from '../../network/bulletin.js'

export default {
    data(){
        return{
            list: [{
                tutoId:1,
                titleContent:'动态规划',
                titleNum:"1.00",
                content:'xxxx内容',
                children:[
                    {
                        tutoId:2,
                        titleContent:'斐波那契',
                        titleNum:"1.01",
                        content:'xxxx内容',
                    },
                     {
                        tutoId:3,
                        titleContent:'找零问题',
                        titleNum:"1.02",
                        content:'xxxx内容',
                    }
                ]
            }],
            problemList:[
            {
                probId:1,
                probTitle:'打家劫舍',
                content:'hello'
            },
            {
                probId:2,
                probTitle:'黑熊过河',
                content:'hi'
            }],
            dataList:[
             {
             }
            ],
            //当前问题管理相关的tutorialid
            currentTutorial:'',
            listLoading: false,
            dialogFormVisible:false,
            problemDialogShow:false,
            dataDialogShow:false,
            editProblemShow:false,
            showText:'添加训练教程',
            showText2:'添加相关练习题',
            showText3:'管理题解',
            editAnswerShow:false,
            editDataShow:false,
            editAInfo:{
                probId:'',
                text:''
            },
            editAInfoRules:{
                content:[
                    {required:true,message:'请输入题解内容！',trigger:'blur'}
                ]
            },
            editAnswerShow:false,
            editInfo:{
               titleContent:'',
               content:'',
               titleNum:''
            },
            editDInfo:{
               probId:'',
               in:'',
               out:'',
               dataId:''
            },
            currentProb:'',
            editDInforRules:{
               in:[
                   {required:true,message:'请输入测试数据的输入数据！',trigger:'blur'}
               ],
               out:[
                   {required:true,message:'请输入测试数据的输出数据！',trigger:'blur'}
               ]
            },
            editInfoRules:{
               titleContent:[
                   {required:true,message:'请输入教程标题！',trigger:'blur'}
               ],
               content:[
                   {required:true,message:'请输入教程内容!',trigger:'blur'}
               ],
               titleNum:[
                   {required:true,message:'请输入教程标号!',trigger:'blur'}
               ]
            },
            editPInfo:{
               probId:'',
               probTitle:'',
               content:''
            },
            editPInfoRules:{
                probTitle:[
                  {required:true,message:'问题标题不能为空!',trigger:'blur'}
                ],
                content:[
                  {required:true,message:'问题内容不能为空!',trigger:'blur'}
                ]
            },
            formLabelWidth: '120px'
        }
        
    },
    methods:{
       test(){
          console.log('test');
       },
       handleAddp()
       {
          this.clearInfo2();
          this.showText2="添加相关练习"
          this.editProblemShow=true;
          console.log('问题添加');
       },
        handleEditP(index, row)
        {
           this.editProblemShow=true;
           this.showText2="修改相关练习"
           this.$turn(this.editPInfo,row);
           console.log('handleEditP')
        },
         handleDeleteP(index, row)
        {
           console.log('handleDeleteP');
           const probId=row.probId;
           const probTitle=row.probTitle;
           console.log(row);
            this.$confirm(`此操作将永久删除问题"${probTitle}", 是否继续?`, '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).then(() => {
            this.$message({
                type: 'success',
                message: '删除成功!'
            });
            }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消删除'
            });          
            });
        },
        handleDetail(index,row)
        {
            const tutoId=row.tutoId;
            console.log(tutoId);
            this.$emit('routeChange0',{to:`/index/tutorialDetail/${tutoId}`,name:'训练教程详情'})
            this.$router.push(`/index/tutorialDetail/${tutoId}`)
        },
        async handleEdit(index, row)
        {
            const tutoId=row.tutoId;
            this.currentTutorial=tutoId;
            console.log(tutoId);
            this.dialogFormVisible=true;
            let res=await getTutorialById(row.tutoId);
            this.$turn(this.editInfo,res.data.data);
            this.showText='修改训练教程'
        },
        handleAdd()
        {
           this.showText='添加训练教程';
           this.clearInfo();
           this.dialogFormVisible=true;
        },
        clearInfo()
        {
           this.editInfo.titleContent='';
           this.editInfo.titleNum='';
           this.editInfo.content='';
        },
        clearInfo2(){
           this.editPInfo.probId='';
           this.editPInfo.probTitle="";
           this.editPInfo.content='';
        },
        //删除教程
        async handleDelete(index,row) {
            const tutoId=row.tutoId;
            const name=row.titleContent
            console.log(tutoId);
            let res=await this.$confirm(`此操作将永久删除教程"${name}", 是否继续?`, '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            });
            console.log(res);
            if(res=="confirm")
            {
                let res1=await deleteTutorial(tutoId);
                console.log(res1);
                if(res1.data.metaInfo.status==200)
                {
                 this.getAllSubject();
                 this.$message({
                    message: '删除教程成功！',
                    type: 'success'
                })
                
                }
                else
                {
                    this.$message({
                    message: '删除教程失败！',
                    type: 'warning'
                })
                }               
            }
      },
      async updateTutorial(){
          let obj={
             tutoId:this.currentTutorial,
             content:this.editInfo.content,
             titleNum:this.editInfo.titleNum
         };
         console.log(obj);
         let data=await updateTutorial(obj);
         console.log(data);
         this.$message({
         message: '修改教程成功！',
         type: 'success'
         });
      },
      async addTutorial(){
         this.dialogFormVisible=false;
         if(this.showText=='修改训练教程')
         {
             this.updateTutorial();
             return;
         }
         console.log(this.editInfo);
         let obj={
             titleContent:this.editInfo.titleContent,
             content:this.editInfo.content,
             titleNum:this.editInfo.titleNum
         };
         let data=await addTutorial(obj);
         console.log(data);
         this.getAllSubject();
         this.$message({
         message: '添加教程成功！',
         type: 'success'
         });
      },
      async handleProblemManage(index,row)
      {
          this.currentTutorial=row.tutoId;
          let res=await getTutorialById(this.currentTutorial);
          let problemList=res.data.data.problemList;
          for(let item of problemList)
          {
              let index=item.content.indexOf('</h4>');
              item.probTitle=item.content.substring(4,index);
          }
        //   console.log(problemList);
          this.problemList=problemList;
          this.problemDialogShow=true;
      },
      //管理Data
      async handleShowData(index, row)
      {
          this.currentProb=row.probId;
          this.dataDialogShow=true;
          let res=await getData(row.probId);
          console.log(res);
          console.log(res.data.data);
          this.dataList=res.data.data;
        //   this.dataList=res.data.data.data;
        //   console.log(this.dataList);
      },
      handleEditD(index, row)
      {
          this.editDataShow=true;
          this.$turn(this.editDInfo,row);
          this.editDInfo.probId=this.currentProb;
          console.log(this.editDInfo);
      },
      async handleDeleteD(index,row)
      {
        let confirm=await this.$confirm(`此操作将永久删除测试数据"${row.dataId}", 是否继续?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
        });
        if(confirm!='confirm')
        return;
         let obj={
             probId:this.currentProb,
             dataIds:row.dataId
         }
         let res=await deleteData(obj);
         console.log(res);
         let res1=await getData(this.currentProb);
         this.dataList=res1.data.data;
         console.log(res1);
         this.$message({
             type:'success',
             message:'删除测试数据成功!'
         })
      },
      //添加测试数据
      handleAddD()
      {
           this.editDataShow=true;
           this.editDInfo.probId=this.currentProb;
           console.log(this.editDInfo);
           this.editDInfo.in='';
           this.editDInfo.out='';
           this.editDInfo.dataId='';
      },
      //显示题解
      async handleShowAnswer(index,row)
      {
          this.editAInfo.probId=row.probId;
          console.log(row.probId);
          this.editAnswerShow=true;
          let res=await getAnswer(row.probId);
          let data=res.data.data;
          this.editAInfo.probId=row.probId;
          console.log(data);
          if(data!=null)
          this.editAInfo.text=data.text;
          else
          this.editAInfo.text='';
          console.log(this.editAInfo);
          this.currentProb=row.probId;
      },
      //修改问题表述
      async updateProblem(){
          let obj={
              probId:this.editPInfo.probId,
              tutoId:this.currentTutorial,
              content:this.editPInfo.content
          }
          console.log(obj);
          let res=await updateProblem(obj);
          console.log("data",res.data.data);
          if(res.data.metaInfo.status==200)
          {
              this.$message({
                  message:'修改问题表述成功',
                  type:'success'
              })
            
          }
          this.editProblemShow=false;
      },
      //添加问题
      async addProblem(){
          if(this.showText2=='修改相关练习')
           {
               this.updateProblem();
               return;
           }
          let res=await addProblem({
              probId:this.editPInfo.probId,
              probTutoId:this.currentTutorial,
              content:this.editPInfo.content
          })
          console.log('添加问题',res);
          this.$message({
              type:'success',
              message:'添加问题成功!'
          })
          this.editProblemShow=false;
          
      },
      async getAllSubject(){
          let res=await getAllTutorialsForOneSubject();
          let titleContentList=res.data.data.titleContentList;
          for(let item of titleContentList)
          {
              await this.getTwoSubject(item);
          }
          this.list=titleContentList;
      },
      async getTwoSubject(obj)
      {
           let res=await getAllTutorialsForTwoSubject(obj.titleNum);
           let titleContentList=res.data.data.titleContentList;
           obj.children=titleContentList;
      },
      //更新测试数据
      async updateData()
      {
          let res=await updateData(this.editDInfo);
          this.editDataShow=false;
          let res1=await getData(this.currentProb);
          console.log(res1);
          this.dataList=res1.data.data;
          this.$message({
              message:'修改测试数据成功',
              type:'success'
          })
          
      },
      async addData()
      {
          if(this.editDInfo.dataId!='')
          {
              this.updateData();
          }
          else
          {
              let res=await addData(this.editDInfo);
              console.log(res);
              let res1=await getData(this.currentProb);
              console.log(res1);
              this.dataList=res1.data.data;
              this.editDataShow=false;
              this.$message({
                  message:'添加测试数据成功',
                  type:'success'
              })
          }
      },
      async testAddData()
      {
          let res=await addData({
               probId:3,
               dataList:[
                  {
                      in:'729\n129',
                      out:'729 is not a multiple of 159'
                  } 
               ]
          })
          console.log(res);
      },
      async testGetData(){
          let res=await getData(3);
          console.log(res);
      },
      async updateAnswer()
      {
          this.editAInfo.answerId=this.currentProb;
          console.log(this.editAInfo);
          let res=await updateAnswer(this.editAInfo);
          console.log(res);
          this.$message({
              type:'success',
              message:'修改题解成功'
          })
          this.editAnswerShow=false;
      },
      async addAnswer()
      {
          if(this.editAInfo.text!='')
          {
             let res=await addAnswer(this.editAInfo);
             console.log(res);
             this.editAnswerShow=false;
             this.$message({
                  type:'success',
                  message:'添加题解成功！'
             })
          }
          else
          {
              this.$message({
                  type:'warning',
                  message:'题解不能为空！'
              })
          }
      },
    },
    created(){
        console.log('hi')
        // this.addData();
        this.getAllSubject();
        // this.testAddData();
        // this.testGetData();
    }
}
</script>

<style scoped>
     .tutorial-manager
     {
         width: 100%;
         position: relative;
     }
     .button-part
     {
         position: absolute;
         top:-60px;
         right:10px;
         z-index:100;
     }
    
</style>