import request from './request.js'

//添加测试数据
export function addData(data) {
    const probId = data.probId;
    let dataList = [{ in: data.in,
        out: data.out
    }]
    console.log(probId);
    console.log(dataList);
    return request({
        method: 'POST',
        url: `/data/add/${probId}`,
        data: {
            dataList
        }
    })
}

//删除测试数据
export function deleteData(data) {
    console.log('数据对象', data);
    const { dataIds, probId } = data;
    return request({
        method: 'DELETE',
        url: `/data/delete/${dataIds}/${probId}`
    })
}

//查看测试数据
export function getData(probId) {
    return request({
        method: 'GET',
        url: `/data/get/${probId}`
    })
}

//修改测试数据
export function updateData(data) {
    const { dataId, probId } = data;
    return request({
        method: 'PUT',
        url: `/data/modify/${dataId}/${probId}`,
        data: { in: data.in,
            out: data.out
        }
    })
}