import request from './request'

//添加问题
export function addProblem(data) {
    return request({
        method: 'post',
        url: '/problem/add',
        data
    })
}

//修改问题表述
export function updateProblem(data) {
    return request({
        method: 'put',
        url: '/problem/modify',
        data
    })
}

//删除问题
export function deleteProblem(probId) {
    return request({
        method: 'delete',
        url: `/problem/delete/${probId}`
    })
}

//查看题解
export function getAnswer(probId) {
    return request({
        method: 'get',
        url: `/answer?problemId=${probId}`
    })
}

//添加题解
export function addAnswer(data) {
    return request({
        method: 'post',
        url: '/answer/add',
        data
    })
}

//更改题解
export function updateAnswer(data) {
    const { answerId, text } = data;
    return request({
        method: 'put',
        url: `/answer/update/${answerId}`,
        data: {
            text
        }
    })
}