import axios from 'axios'
import request from './request.js'


//获取所有一级教程标题
export function getAllTutorialsForOneSubject() {
    return request({
        method: 'get',
        url: '/tutorial/first'
    })
}

//获取相应二级教程标题
export function getAllTutorialsForTwoSubject(titleNum) {
    return request({
        method: 'get',
        url: `/tutorial/second/${titleNum}`
    })
}

//获取某个特定教程的详情信息
export function getTutorialById(tutoId) {
    return request({
        method: 'get',
        url: `/tutorial/view?tutoId=${tutoId}`
    })
}

//添加训练教程
export function addTutorial(data) {
    return request({
        method: 'post',
        url: '/tutorial/add',
        data
    })
}

//删除训练教程
export function deleteTutorial(tutoId) {
    return request({
        method: 'delete',
        url: `/tutorial/delete/${tutoId}`
    })
}

//修改训练教程
export function updateTutorial(data) {
    const { tutoId, content } = data;
    return request({
        method: 'put',
        url: `/tutorial/modify/${tutoId}`,
        data: {
            content
        }
    })
}